export const routers = [
  {
    path: '/',
    redirect: "/home",
    component: resolve => {
      require(['../pages/index'], resolve);
    },
    children: [
      {
        path: 'home',
        name:"name",
        component: resolve => {
          require(['../pages/home/index'], resolve);
        },
      },
      {
        path: 'article',
        name:"article",
        component: resolve => {
          require(['../pages/article/index'], resolve);
        },
      },
      {
        path: 'about',
        name:"about",
        component: resolve => {
          require(['../pages/about/index'], resolve);
        },
      }
    ]
  }
];



